import Vue from 'vue'
import App from './App'
import router from './router'
import rightMenuPro from 'vue-right-menu/src/index.js'
import 'vue-right-menu/src/index.css'

Vue.use(rightMenuPro)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  render: h => h(App)
})



